$blue: #30C5FF;
$black: #1a1a1a;
$white: #fafafa;



*{
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

html{
	transition:0.5s ease;
	font-family: 'Nunito', sans-serif;
}
body{
	transition:0.5s ease;

}

#header{
  width: 100vw;
  display: block;
  position: fixed;
  z-index: 99999;
  height: 80px;
  background: transparent;
//   background-image: linear-gradient(#000,transparent);
  background-size: 200%;
}

#header-wrap{
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  padding: 0 100px;
}



/* MAIN-NAV START ///*/

#main-nav{
	width: max-content;
}
#main-nav nav{

}

#main-nav nav ul{
	display: flex;
	flex-direction: row;
	align-items: center;
	align-content: center;
}

#main-nav nav ul li{
	margin: 0 15px;
	list-style: none;
}

#main-nav nav ul li a{
	text-decoration: none;
	color:#FFF;
	font-size: 18px;
	transition: 0.3s ease;
}

.dark-light-mode-switcher-container{
	display: flex;
	flex-direction: row;
	align-items: center;
	align-content: center;
	justify-content: space-between;
}

.dark-light-mode-switcher{
	position: relative;
	width: 55px;
	height: 25px;
	border-radius: 100px;
	border: 1px solid #5a5a5a;
	margin:0 5px;
	padding: 2px;
	box-shadow: inset 0 3px 5px -3px $black;
	transition: 0.3s ease;
}

.dark-light-mode-point{
	width: 19px;
	height: 19px;
	background-color: $white;
	border-radius: 100px;
	position: absolute;
	// margin: 0 auto 0 0;
	transition: 0.13s ease-out;
	box-shadow: inset 0px -1px 1px 0px #00000090;
}

.dark-light-mode-switcher.light-mode{
	background-color:$white;

}

.dark-light-mode-switcher.dark-mode{
	background-color: $blue;
}

.dark-light-mode-point.light-mode{
	left: 2px;
}
.dark-light-mode-point.dark-mode{
	left: 32px;
}



/* MAIN-NAV STOP ///*/

/* MOBILE-NAV START ///*/

#menu-mobile{
	display: none;
}

/* MOBILE-NAV STOP ///*/



/* MENU BURGER START ///*/

.menu-burger {
	
	background-color: transparent;
	border: none;
	cursor: pointer;
	display: flex;
	padding: 0;
}
.menu-line{
	fill: none;
	stroke: $black;
	stroke-width: 6;
	transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1),
	stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1);
}
.menu-line1 {
	stroke-dasharray: 60 207;
	stroke-width: 6;
}
.menu-line2 {
	stroke-dasharray: 60 60;
	stroke-width: 6;
}
.menu-line3 {
	stroke-dasharray: 60 207;
	stroke-width: 6;
}
.opened .menu-line1 {
	stroke-dasharray: 90 207;
	stroke-dashoffset: -134;
	stroke-width: 6;
}
.opened .menu-line2 {
	stroke-dasharray: 1 60;
	stroke-dashoffset: -30;
	stroke-width: 6;
}
.opened .menu-line3 {
	stroke-dasharray: 90 207;
	stroke-dashoffset: -134;
	stroke-width: 6;
}

/* MENU BURGER STOP ///*/



#page-wrap{
	// padding-top: 80px;
	// height: 100vh;
	width: 100%;
}






/* HERO SECTION */

#hero-section{
	display: block;
	position: relative;
}


#hero-section-content{
	z-index: 10;
	position: absolute;
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
    align-items: center;
    align-content: center;
	padding: 300px 0 0 0;
}

#hero-section-buttons{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	align-content: center;
}

#hero-login-button{
	padding: 10px 60px;
    border-radius: 1000px;
    background-color: #319e56;
    color: #FFF;
    outline: none;
    border: 2px solid #ffffff;
    font-size: 20px;
}

#hero-learn-more-button{
	padding: 10px 60px;
	background:transparent;
	border:2px solid #ffffff;
	font-size: 20px;
	color:#FFFFFF;
	border-radius: 1000px;
}